import { Link, useLoaderData } from "remix";
import { Post, getPosts } from "~/post";

export const loader = () => {
  return getPosts;
};

export default function Posts() {
  const posts: Post[] = useLoaderData();
  console.log(posts);

  return (
    <div className="p-10">
      <h1 className="text-3xl font-normal leading-normal mt-0 mb-2">Posts</h1>
      {posts.map((post) => (
        <div>
          <Link to={post.slug}>{post.title}</Link>
        </div>
      ))}
    </div>
  );
}
